.top-bar{
  background-color: rgb(6, 16, 27);
  display: flex;
  flex-direction: row;
  width: 100vw;
  overflow-x: hidden;
  justify-content:space-evenly;
  align-items: center;
  /* padding: 2rem 3rem; */
}

.nav-links{

  display: flex;
  flex-direction: row;
  gap: 1.5rem;
  align-items: center;
}
@media screen and (max-width: 768px) {
  .nav-links {
    display: none;
  }
}

.hamburger-btn{
  display: none;
  cursor: pointer;
  z-index: 30;
}
@media screen and (max-width: 768px) {
  .hamburger-btn {
    display: block;
    position: relative;
    width: 30px;
    height: 20px;
  }
  .top-bar{
    justify-content: space-between;
    padding:  1rem;
  }
  .hamburger-btn div {
    position: absolute;
    width: 100%;
    height: 3px;
    background-color: white;
    border-radius: 5px;
    transition: all 0.3s ease-in-out;
  }
  .hamburger-btn div:nth-child(1) {
    top: 0;
  }
  .hamburger-btn div:nth-child(2) {
    top: 8px;
  }
  .hamburger-btn div:nth-child(3) {
    top: 16px;
  }
}
.navbar {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  justify-content: center;
  margin: auto;
  padding-top: 23px;
}

.navbar ul {
  display: flex;
  gap: 1.25rem;
  padding: 0.8rem;
  border-radius: 25px;
  background: #FFFFFF1A;
  backdrop-filter: blur(35px);
  border-radius: 32px;
  border: 1px solid rgba(255, 255, 255, 0.4);
  background: #ffffff1a;
  backdrop-filter: blur(25px);
  box-shadow: 3px 3px 6px rgba(0,0,0,0.25);
  transition: transform 0.3s ease, box-shadow 0.3s ease, background 0.3s ease;
  color: white;
  font-weight: 500;
  font-size: 0.85rem;
  text-transform: uppercase;
  letter-spacing: 0.08em;
  list-style: none;
  transition: all 0.3s ease;
}

.navbar ul li {
  cursor: pointer;
  position: relative;
  transition: all 0.3s ease;
}

.navbar ul li:hover {
  color: white;
  transform: scale(1.05) translateY(-1px);
}

.navbar ul li::after {
  content: '';
  position: absolute;
  width: 0%;
  height: 1.5px;
  left: 0;
  bottom: -3px;
  background: #ffffffaa;
  transition: width 0.3s ease;
}

.navbar ul li:hover::after {
  width: 100%;
}

/* Desktop navbar links */
.navbar ul li a,
.mobile-sidebar ul li a {
  color: #fff;
  text-decoration: none;
  font-weight: 500;
  transition: color 0.3s;
}

/* Hover effect for both */
.navbar ul li a:hover,
.mobile-sidebar ul li a:hover {
  color: #ffcc00;
}

/* Optional: if mobile sidebar background is dark, you may need to increase contrast */
.mobile-sidebar ul li a {
  display: block;
  padding: 10px 0;
}
.logo{
  height:40px;
  width:auto;
}
/* AUTH BUTTONS */
.auth-buttons {
  display: flex;
  gap: 0.5rem;
}

.signup-btn, .login-btn {
  padding: 0.6rem 1.2rem;
  font-size: 0.85rem;
  border-radius: 20px;
  border: 1px solid rgba(255,255,255,0.3);
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s;
  text-align: center;
  padding-bottom: 0.7rem;
}

.signup-btn {
  background: rgba(255,255,255,0.1);
  color: white;
}

.signup-btn:hover {
  transform: scale(1.05);
  background: rgba(255,255,255,0.2);
  box-shadow: 0 3px 5px rgba(0,0,0,0.25);
}

.login-btn {
  background: white;
  color: black;
}

.login-btn:hover {
  transform: scale(1.05);
  box-shadow: 0 3px 5px rgba(0,0,0,0.25);
}

/* SIDEBAR */

/* Mobile sidebar (from right) */
.mobile-sidebar {
  position: fixed;
  top: 0;
  right: -100%;
  width: 220px;
  height: 100%;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(12px) saturate(180%);
  -webkit-backdrop-filter: blur(12px) saturate(180%);
  border-left: 1px solid rgba(255, 255, 255, 0.2);
  padding: 2rem 1.2rem;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  transition: right 0.35s cubic-bezier(0.4, 0, 0.2, 1);
  z-index: 25;
  box-shadow: -6px 0 25px rgba(0,0,0,0.4);
  overflow-y: auto;
}

.mobile-sidebar.open {
  right: 0;
}

/* Close button */
.mobile-sidebar .close-btn {
  align-self: flex-end;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.mobile-sidebar .close-btn:hover {
  transform: rotate(90deg) scale(1.1);
}

/* Sidebar links */
.mobile-sidebar ul {
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  color: white;
  font-weight: 500;
  font-size: 0.85rem;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.mobile-sidebar ul li {
  cursor: pointer;
  transition: transform 0.3s ease, color 0.3s ease, text-shadow 0.3s ease;
}

.mobile-sidebar ul li:hover {
  transform: scale(1.03) translateX(2px);
  color: #fffb;
  text-shadow: 0 1px 4px rgba(255,255,255,0.4);
}

/* Mobile auth buttons */
.mobile-auth-buttons {
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
}

.mobile-auth-buttons .signup-btn,
.mobile-auth-buttons .login-btn {
  width: 100%;
  font-size: 0.8rem;
  padding: 0.5rem 0;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  border-radius: 16px;
}

.mobile-auth-buttons .signup-btn:hover,
.mobile-auth-buttons .login-btn:hover {
  transform: scale(1.05);
  box-shadow: 0 6px 15px rgba(255,255,255,0.3);
}

/* Overlay backdrop */
.sidebar-backdrop {
  position: fixed;
  inset: 0;
  background: rgba(0,0,0,0.5);
  backdrop-filter: blur(2px);
  z-index: 20;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease;
}

.sidebar-backdrop.active {
  opacity: 1;
  pointer-events: all;
}